.game-hero {
    position: relative;
    margin: 2rem 0;

    &__backgroundImg {
        width: 100%;
        height: 50vw;
        object-fit: cover;
    }

    &__websiteBtn {
        position: absolute;
        bottom: 2rem;
        right: 2rem;
        padding: 0.75rem;
        background: $secondary-color;
        border: none;
        color: #fff;
        text-decoration: none;
        font-size: 400;
        font-weight: bold;

        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__websiteBtn p {
        margin-right: 2rem
    }
}

.game-detail {

    *{
        padding: 0.5rem 0;
    }

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: end;

        &__ratings {
            color: $secondary-color;
        }
    }

    &__release {
        display: flex;
        justify-content: start;

        * {
            flex: 1 ;
            margin-right: 1rem;
        }
    }

    &__classification {
        display: flex;
        justify-content: start;

        * {
            flex: 1 ;
            margin-right: 1rem;
        }
    }
}

.game-buy {
    h1{
        color: $secondary-color;
    }

    &__stores {
        padding: 2rem 0;

        &__store {
            display: flex;
            padding: 0.5rem 0;

            a {
                color: #fff;
                text-decoration: none;
                border-bottom: 0.25rem solid $secondary-color;
                margin-right: .5rem;
            }
        }
    }
}

.game-trailer {
    h1 {
       color: $secondary-color; 
    }

    &__video {
        width: 100%;
        margin: 2rem 0;
    }
}

.game-screenshots {
    padding: 2rem 0;

    h1 {
        color: $secondary-color;
    }

    &__pictures {
        display: grid;
        grid-template-columns: repeat(2, 2fr);
        grid-gap: 40px;
        padding-top: 2rem;

        img {
            width: 100%;
            height: 25vw;
            object-fit: cover;
        }
    }
}

.youtube {
    h1 {
        color: $secondary-color;
    }
}

.similar-games {
    h1 {
        color: $secondary-color;
    }
}
